import React from 'react'
import Navbar from '../Navbar/Navbar'

const GiveLink = ({ id, quizId }) => {
	const link = `http://localhost:3000/quiz/${id}/${quizId}`

	return (
		<div className='text-center mt-5'>
			<h1>
				  {' '}
				<a className='fs-4' href={link}>
					http://localhost:3000/quiz/{id}/{quizId}
				</a>
			</h1>
		</div>
	)
}

export default GiveLink
