import React from 'react'
import Lottie from 'react-lottie'
import styled from 'styled-components'

import * as animationData from '../../assets/Blogging.json'

const Header = () => {
	const defaultOptions = {
		loop: true,
		autoplay: true,
		animationData: animationData,
		rendererSettings: {
			preserveAspectRatio: 'xMidYMid slice',
		},
	}

	return (
		<Wrapper className='container position-relative'>
			<Title className='mt-5  pt-5'>InteractiveMod</Title>
			<SubTitle className='mt-4 display-5 fs-2'>
				  ,    
				   
			</SubTitle>
			<ButtonGroup className='mt-5'>
				<button className='btn bg-primary mx-2 text-white px-4 py-2'>
					 
				</button>
				<button className='btn bg-danger mx-2 text-white px-4 py-2'>
					  
				</button>
			</ButtonGroup>

			<BloggingAnimationWrapper>
				<Lottie
					className='Blogging-animation'
					options={defaultOptions}
					height={400}
					width={500}
				/>
			</BloggingAnimationWrapper>
		</Wrapper>
	)
}

const Wrapper = styled.div``

const Title = styled.h1`
	margin-top: 100px !important;
`

const SubTitle = styled.p`
	max-width: 750px;
`

const ButtonGroup = styled.div`
	button {
		border-radius: 40px;
	}
`

const BloggingAnimationWrapper = styled.div`
	position: absolute;
	top: -40px;
	right: 10px;
	display: flex;

	justify-content: center;
	align-items: center;

	@media (max-width: 958px) {
		display: none;
	}
`

export default Header
