import React from 'react'
import styled from 'styled-components'
import Lottie from 'react-lottie'
import { useNavigate } from 'react-router-dom'

import * as quizJson from '../../assets/quiz.json'
import * as monkey from '../../assets/monkey.json'

const GamesSection = () => {
	const navigate = useNavigate()

	const quizLottieOptions = {
		loop: true,
		autoplay: true,
		animationData: quizJson,
		rendererSettings: {
			preserveAspectRatio: 'xMidYMid slice',
		},
	}

	const monkeyLottieOptions = {
		loop: true,
		autoplay: true,
		animationData: monkey,
		rendererSettings: {
			preserveAspectRatio: 'xMidYMid slice',
		},
	}

	return (
		<Wrapper>
			<Title className='text-center fs-2'> </Title>

			<GamesSectionWrapper>
				<GamesSectionContainer className='container text-white'>
					<Card
						onClick={() => navigate('/create/quiz')}
						className='bg-light shadow'
					>
						<Lottie options={quizLottieOptions} width={250} height={250} />
						<CardTitle className='text-dark mt-2 text-center px-4'>
							<h4>  </h4>
						</CardTitle>
					</Card>
					<Card className='bg-light shadow'>
						<Lottie options={monkeyLottieOptions} width={250} height={250} />
						<CardTitle className='text-dark mt-2 text-center px-4'>
							 
						</CardTitle>
					</Card>
					<Card className='bg-light shadow'>
						<Lottie options={monkeyLottieOptions} width={250} height={250} />
						<CardTitle className='text-dark mt-2 text-center px-4'>
							 
						</CardTitle>
					</Card>
				</GamesSectionContainer>
			</GamesSectionWrapper>
		</Wrapper>
	)
}

const Wrapper = styled.div`
	padding: 50px 0px;
`

const GamesSectionWrapper = styled.div`
	width: 100%;
`

const GamesSectionContainer = styled.div`
	display: flex;
	justify-content: space-around;
	padding: 40px;

	@media (max-width: 958px) {
		flex-wrap: wrap;
	}
`

const Card = styled.div`
	width: 400px;
	height: 400px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	cursor: pointer;

	transition: all ease 0.3s;

	margin: 0px 5px;

	&:hover {
		transform: scale(1.1);
	}

	@media (max-width: 958px) {
		margin: 20px 0px;
	}
`

const CardTitle = styled.p``

const Title = styled.p``

export default GamesSection
