import React from 'react'
import styled from 'styled-components'

const QuizHeader = ({ text }) => {
	return (
		<Wrapper>
			<Title className='display-1 fs-2 mt-5 text-center'>{text}</Title>
		</Wrapper>
	)
}

const Wrapper = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	height: 200px;
`

const Title = styled.p`
	max-width: 800px;
`

export default QuizHeader
