import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import styled from 'styled-components'

import { selectUser } from '../../features/userSlice'
import { writeResultQuiz } from '../../service/firebase'

import './circular.css'

const QuizResults = ({ results }) => {
	const { userid: creatorid, quizid } = useParams()
	const { user } = useSelector(selectUser)

	const [score, setScore] = useState(0)
	const [allQuestions, setAllQuestion] = useState(0)
	const [correctAnswers, setCorrectAnswers] = useState(0)
	const [wrongAnswers, setWrongAnswers] = useState(0)

	const countScore = arr => {
		const resultArr = arr.filter(item => item.isTrue === true)
		const wrongAnswersArr = arr.filter(item => item.isTrue === false)

		const _score = Math.floor((resultArr.length / arr.length) * 100)

		setTimeout(() => {
			setScore(_score)
		}, 1500)
		setAllQuestion(arr.length)
		setCorrectAnswers(resultArr.length)
		setWrongAnswers(wrongAnswersArr.length)

		moment.locale('ru')
		const endedTime = moment().format('LL')

		writeResultQuiz(creatorid, quizid, user.uid, {
			userId: user.uid,
			userName: user.displayName,
			userEmail: user.email,
			userImg: user.photoURL,
			correctAnswers: resultArr,
			wrongAnswers: wrongAnswersArr,
			score: _score,
			endedTime: endedTime,
			results: [...results],
		})
	}

	useEffect(() => {
		countScore(results)
	}, [])

	console.log(results)

	return (
		<Wrapper>
			<CircularWrapper>
				<CircularProgressbar
					value={score}
					text={`${score}%`}
					circleRatio={0.75}
					className='circular-rotate'
					styles={buildStyles({
						rotation: 1 / 2 + 1 / 8,
						strokeLinecap: 'butt',
						trailColor: '#eee',
						pathTransitionDuration: 4,
					})}
				/>
			</CircularWrapper>

			<h2 className='mt-3'>  {score}%</h2>

			<ResultWrapper className='container bg-light m-3'>
				<h2 className='text-center display-5 fs-2 mb-5'>  </h2>
				<p className='fs-5'>
					 
					<span className='ms-auto'>{allQuestions}</span>
				</p>
				<p className='fs-5'>
					  
					<span className='ms-auto'>{allQuestions}</span>
				</p>
				<p className='fs-5'>
					  
					<span className='ms-auto'>{correctAnswers}</span>
				</p>
				<p className='fs-5'>
					  
					<span className='ms-auto'>{wrongAnswers}</span>
				</p>
			</ResultWrapper>
		</Wrapper>
	)
}

const Wrapper = styled.div`
	width: 100vw;
	min-height: 100vh;
	display: flex;
	align-items: center;
	flex-direction: column;
	background-color: #fff;
`

const CircularWrapper = styled.div`
	width: 230px;
	height: 230px;

	margin-top: 60px;
`

const ResultWrapper = styled.div`
	padding: 40px 50px;
	max-width: 800px !important;

	p {
		display: flex;
	}
`

export default QuizResults
