import React, { useEffect, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar'
import Lottie from 'react-lottie'
import styled from 'styled-components'

import { getOneQuizFromDb } from '../../service/firebase'
import { peopleIconOptions, quizIconOptions, removeIconOptions } from './Icon'

import Navbar from '../Navbar/Navbar'

const ProfileQuiz = () => {
	const { userid, quizid } = useParams()
	const [quiz, setQuiz] = useState({})
	const [leaderBoard, setLeaderBoard] = useState([])

	let navigate = useNavigate()

	useEffect(() => {
		getOneQuizFromDb(userid, quizid).then(res => {
			if (!res) {
				navigate('/')
			}

			const arrayOfObj = Object.entries(res?.results)
				.map(e => ({ ...e[1] }))
				.sort((a, b) => (a.score > b.score ? -1 : 1))

			setLeaderBoard(arrayOfObj)
			setQuiz(res)
		})
	}, [])

	console.log(quiz)

	return (
		<>
			<Navbar />
			<Wrapper className='bg-light'>
				<Container className='container'>
					<h1 className='pt-4'> </h1>

					<ControlWrapper className='d-flex justify-content-between align-items-center pt-3'>
						<Card className='shadow border bg-primary text-light text-center'>
							<h2 className='mt-5'>Development</h2>
						</Card>
						<Card className='shadow border'>
							<Lottie options={peopleIconOptions} height={170} width={170} />
							<strong className='fs-4 mt-2'>42 </strong>
						</Card>
						<Card className='shadow border'>
							<Lottie options={quizIconOptions} height={170} width={170} />

							<button className='btn btn-primary mt-2'> </button>
						</Card>
						<Card className='shadow border'>
							<Lottie options={removeIconOptions} height={170} width={170} />

							<button className='btn btn-danger mt-2'> </button>
						</Card>
					</ControlWrapper>

					<TableWrapper>
						<h2 className='my-3'> </h2>

						{leaderBoard.length === 0 ? (
							<h3 className='m-5 text-center'>    .</h3>
						) : (
							<Table className='border border-2 px-5 py-4 shadow'>
								<table className='table'>
									<thead className='thead-dark'>
										<tr>
											<th scope='col'>#</th>
											<th scope='col'></th>
											<th scope='col'></th>
											<th scope='col'> </th>
											<th scope='col'></th>
										</tr>
									</thead>
									<Tbody>
										{leaderBoard.map((item, index) => {
											return (
												<tr key={index}>
													<th scope='row'>{index + 1}</th>
													<td>
														<img
															className='rounded rounded-circle'
															src={item.userImg}
															alt='user'
														/>
														<span className='ms-3'>{item.userName}</span>
													</td>
													<td>
														<CircularWrapper>
															<CircularProgressbar
																value={item.score}
																text={`${item.score}%`}
																circleRatio={0.75}
																className='circular-rotate'
																styles={buildStyles({
																	rotation: 1 / 2 + 1 / 8,
																	strokeLinecap: 'butt',
																	trailColor: '#eee',
																	pathTransitionDuration: 4,
																	textSize: '24px',
																	textColor: 'black',
																})}
															/>
														</CircularWrapper>
													</td>
													<td>{item.endedTime}</td>
													<td>
														<button className='btn-sm border btn-primary'>
															
														</button>
													</td>
												</tr>
											)
										})}
									</Tbody>
								</table>
							</Table>
						)}
					</TableWrapper>
				</Container>
			</Wrapper>
		</>
	)
}

const Wrapper = styled.div`
	width: 100%;
	min-height: 100vh;
`

const Container = styled.div``

const ControlWrapper = styled.div``

const Card = styled.div`
	width: 300px;
	height: 300px;

	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
`

const Table = styled.div``

const TableWrapper = styled.div`
	display: inline-block;
	width: 100%;
	margin: 50px 0px;
`

const Tbody = styled.tbody`
	tr {
		th {
			vertical-align: middle;
		}

		td {
			vertical-align: middle;

			img {
				width: 40px;
				height: 40px;
				object-fit: cover;
			}
		}
	}
`

const CircularWrapper = styled.div`
	width: 50px;
	height: 50px;
`
export default ProfileQuiz
