import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import Lottie from 'react-lottie'
import { useNavigate, useParams } from 'react-router-dom'
import { countOptions, defaultOptions, rocketOptions } from './Icon'

import Navbar from '../Navbar/Navbar'
import { useSelector } from 'react-redux'
import { selectUser } from '../../features/userSlice'
import { signInWithGoogle } from '../../service/firebase'

const HowToQuiz = () => {
	const { user } = useSelector(selectUser)
	const [isCounting, setIsCounting] = useState(false)
	const navigate = useNavigate()
	const { userid, quizid } = useParams()

	const goToQuiz = () => {
		setIsCounting(true)

		setTimeout(() => {
			navigate(`/quiz/start/${userid}/${quizid}`)
		}, 4000)
	}

	useEffect(() => {
		if (!user) {
			signInWithGoogle()
		}
	}, [])

	return (
		<Wrapper className='bg-light'>
			{!isCounting ? (
				<>
					<Navbar />

					<Container className='container mt-5 py-5 px-5 mb-5 border border-2 shadow'>
						<h1 className='text-center display-5'>   </h1>

						<p className='fs-5 mt-5'>   :</p>

						<div className='d-flex fs-5 align-items-center border-bottom border-3 pb-5'>
							<ul>
								<li>
									   ,      
									 .
								</li>
								<li>     25 .</li>
								<li>   4 .</li>

								<li className='mt-5'>
									   ,      
									 .
								</li>
								<li>     25 .</li>
								<li>   4 .</li>
							</ul>

							<Lottie options={defaultOptions} height={300} width={300} />
						</div>

						<div className='d-flex fs-5 align-items-center  pb-3'>
							<Lottie options={rocketOptions} height={600} width={600} />

							<ul>
								<li>
									   ,      
									 .
								</li>
								<li>     25 .</li>
								<li>   4 .</li>

								<li className='mt-5'>
									   ,      
									 .
								</li>
								<li>     25 .</li>
								<li>   4 .</li>
							</ul>
						</div>

						<div className='text-center'>
							<button
								onClick={goToQuiz}
								className='btn btn-primary shadow fs-5'
							>
								
							</button>
						</div>
					</Container>
				</>
			) : (
				<CounterWrapper>
					<Lottie options={countOptions} height={600} width={600} />
				</CounterWrapper>
			)}
		</Wrapper>
	)
}

const Wrapper = styled.div`
	width: 100%;
	min-height: 100vh;
	background-color: #fff;
`

const Container = styled.div``

const CounterWrapper = styled.div`
	width: 100%;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
`

export default HowToQuiz
