import Header from './Header'
import WhySection from './WhySection'

const Main = () => {
	return (
		<>
			<Navbar />

			<Header />

			<Content>
				<WhySection />
				<GamesSection />
			</Content>
		</>
	)
}

const Content = styled.div``

export default Main
