import { initializeApp } from 'firebase/app'
import { getDatabase, ref, child, get, set } from 'firebase/database'
import {
	getAuth,
	GoogleAuthProvider,
	signInWithPopup,
	onAuthStateChanged,
	signOut,
} from 'firebase/auth'

import store from '../App/store'
import { setUser } from '../features/userSlice'
import { setLoading } from '../features/loadingSlice'

const firebaseConfig = {
	apiKey: 'AIzaSyAedA4h6UnUDCxXeksSXNgzlOVsjNvYrJc',
	authDomain: 'testing-ff2d9.firebaseapp.com',
	databaseURL:
		'https://testing-ff2d9-default-rtdb.europe-west1.firebasedatabase.app',
	projectId: 'testing-ff2d9',
	storageBucket: 'testing-ff2d9.appspot.com',
	messagingSenderId: '740901887606',
	appId: '1:740901887606:web:94d7e7a229f3701ba60957',
}

const app = initializeApp(firebaseConfig)
const db = getDatabase()
const dbRef = ref(db)

export const auth = getAuth(app)

const provider = new GoogleAuthProvider()

onAuthStateChanged(auth, user => {
	if (user) {
		store.dispatch(setUser(user))
	} else {
		store.dispatch(setUser(user))
	}
})

export const signInWithGoogle = () => {
	signInWithPopup(auth, provider)
		.then(result => {
			store.dispatch(setLoading(true))
			store.dispatch(setUser(result.user))
			setTimeout(() => {
				store.dispatch(setLoading(false))
			}, 2000)
		})
		.catch(error => console.log(error))
}

export const signOutGoogle = () => {
	signOut(auth)
		.then(() => {
			store.dispatch(setLoading(true))
			store.dispatch(setUser(null))
			setTimeout(() => {
				store.dispatch(setLoading(false))
			}, 2000)
		})
		.catch(error => {
			console.log(error)
		})
}

export const getAllQuizFromDb = userId => {
	return get(child(dbRef, `${userId}/quizzes/`))
		.then(snapshot => {
			if (snapshot.exists()) {
				return snapshot.val()
			}
		})
		.catch(error => {
			console.error(error)
		})
}

export const getOneQuizFromDb = (userId, quizId) => {
	return get(child(dbRef, `${userId}/quizzes/${quizId}`))
		.then(snapshot => {
			if (snapshot.exists()) {
				return snapshot.val()
			}
		})
		.catch(error => {
			console.error(error)
		})
}

export const writeQuizData = (userId, quizId, quiz) => {
	set(ref(db, `${userId}/quizzes/` + quizId), quiz)
		.then(() => console.log('saved'))
		.catch(err => console.log('errrro'))
}

export const writeResultQuiz = (creatorId, quizId, userId, results) => {
	set(ref(db, `${creatorId}/quizzes/${quizId}/results/` + userId), results)
		.then(() => console.log('saved'))
		.catch(err => console.log('errrro'))
}
