import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'
import styled from 'styled-components'
import { selectUser } from '../../features/userSlice'
import { getOneQuizFromDb } from '../../service/firebase'
import CorrectAnswer from '../CorrectAnswer/CorrectAnswer'
import WrongAnswer from '../WrongAnswer/WrongAnswer'
import QuizBottom from './QuizBottom'
import QuizHeader from './QuizHeader'
import QuizResults from './QuizResults'

const Quiz = () => {
	const { user } = useSelector(selectUser)

	const [currentQuestion, setCurrentQuestion] = useState(0)
	const [isCorrectAnswer, setIsCorrectAnswer] = useState(false)
	const [isWrongAnswer, setIsWrongAnswer] = useState(false)
	const [isEnd, setIsEnd] = useState(false)
	const [results, setResults] = useState([])

	const [questions, setQuestion] = useState([
		{
			questionText: null,
			answersOptions: [
				{ answerText: null, isCorrect: false },
				{ answerText: null, isCorrect: false },
				{ answerText: null, isCorrect: true },
				{ answerText: null, isCorrect: false },
			],
		},
	])

	const { userid, quizid } = useParams()
	const navigate = useNavigate()

	useEffect(() => {
		if (!user) {
			navigate(`/quiz/${userid}/${quizid}`)
		}

		getOneQuizFromDb(userid, quizid).then(res => {
			if (res === undefined) {
				navigate('/')
			} else {
				setQuestion(res.quiz)
			}
		})
	}, [])

	const selectAnswer = (selectedAnswer, isTrue) => {
		if (isTrue) {
			setIsCorrectAnswer(true)
			setResults(prev => [
				...prev,
				{ ...questions[currentQuestion], userAnswer: selectedAnswer, isTrue },
			])

			const nextQuestion = currentQuestion + 1

			if (nextQuestion < questions.length) {
				setCurrentQuestion(nextQuestion)
			} else {
				setTimeout(() => {
					setIsEnd(true)
				}, 1700)
			}

			setTimeout(() => {
				setIsCorrectAnswer(false)
			}, 1500)
		} else {
			setIsWrongAnswer(true)
			setResults(prev => [
				...prev,
				{ ...questions[currentQuestion], userAnswer: selectedAnswer, isTrue },
			])

			const nextQuestion = currentQuestion + 1

			if (nextQuestion < questions.length) {
				setCurrentQuestion(nextQuestion)
			} else {
				setTimeout(() => {
					setIsEnd(true)
				}, 1700)
			}

			setTimeout(() => {
				setIsWrongAnswer(false)
			}, 1700)
		}
	}

	return (
		<Wrapper>
			{isEnd ? (
				<QuizResults results={results} />
			) : (
				<>
					<Container className='container'>
						<QuizWrapper className='border shadow'>
							<QuizHeader text={questions[currentQuestion].questionText} />
							<QuizBottom
								questions={questions[currentQuestion].answersOptions}
								selectAnswer={selectAnswer}
							/>
						</QuizWrapper>
					</Container>

					{isCorrectAnswer && <CorrectAnswer />}
					{isWrongAnswer && <WrongAnswer />}
				</>
			)}
		</Wrapper>
	)
}

const Wrapper = styled.div`
	width: 100vw;
	min-height: 100vh;
	background: #9796f0; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#fbc7d4,
		#9796f0
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#fbc7d4,
		#9796f0
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
`

const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
`

const QuizWrapper = styled.div`
	width: 1000px;
	height: 550px;
	background-color: #fff;
	border-radius: 15px;
`

export default Quiz
