import React from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { signInWithGoogle } from '../../service/firebase'
import ProfilePopup from '../ProfilePopup/ProfilePopup'
import {
	selectProfilePopup,
	setProfilePopup,
} from '../../features/ProfilePopupSlice'

const Links = ({ user }) => {
	const dispatch = useDispatch()
	const { profilePopup } = useSelector(selectProfilePopup)

	const toggleProfile = () => dispatch(setProfilePopup(!profilePopup))

	return (
		<ul className='nav justify-content-center'>
			<li className='nav-item'>
				<Link to='/' className='nav-link text-dark' href='#'>
					
				</Link>
			</li>
			<li className='nav-item'>
				<Link to='/about' className='nav-link text-dark' href='#'>
					 
				</Link>
			</li>
			{user.user && (
				<li className='nav-item'>
					<a className='nav-link text-dark' href='#'>
						{user.user.displayName}
					</a>
				</li>
			)}
			<li className='nav-item'>
				{user.user ? (
					<>
						<img
							src={user.user.photoURL}
							alt='...'
							className='avatar'
							onClick={toggleProfile}
						/>
						{profilePopup && <ProfilePopup />}
					</>
				) : (
					<a onClick={signInWithGoogle} className='nav-link text-dark' href='#'>
						Login With Google
					</a>
				)}
			</li>
		</ul>
	)
}

export default Links
