import React, { useEffect } from 'react'
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { selectUser } from './features/userSlice'
import { selectLoading, setLoading } from './features/loadingSlice'

import Main from './components/Main/Main'
import About from './components/About/About'
import Profile from './components/Profile/Profile'
import ProfileQuiz from './components/ProfileQuiz/ProfileQuiz'
import CreateQuiz from './components/CreateQuiz/CreateQuiz'
import HowToQuiz from './components/HowToQuiz/HowToQuiz'
import Quiz from './components/Quiz/Quiz'
import Loading from './components/Loading/Loading'

import './App.css'

const App = () => {
	const { user } = useSelector(selectUser)
	const { loading } = useSelector(selectLoading)
	const dispatch = useDispatch()

	useEffect(() => {
		setTimeout(() => {
			dispatch(setLoading(false))
		}, 2000)
	}, [])

	return (
		<BrowserRouter>
			{!loading ? (
				<div className='App'>
					<Routes>
						<Route path='/' element={<Main />} />
						<Route path='/about' element={<About />} />
						{user?.uid && <Route path='/profile' element={<Profile />} />}
						{user?.uid && (
							<Route
								path='/profile/quiz/:userid/:quizid'
								element={<ProfileQuiz />}
							/>
						)}

						<Route path='/create/quiz' element={<CreateQuiz />} />
						<Route path='/quiz/:userid/:quizid' element={<HowToQuiz />} />
						<Route path='/quiz/start/:userid/:quizid' element={<Quiz />} />
						<Route path='*' element={<Navigate to='/' />} />
					</Routes>
				</div>
			) : (
				<Loading />
			)}
		</BrowserRouter>
	)
}

export default App
