import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import moment from 'moment'
import 'moment/locale/ru'
import { selectUser } from '../../features/userSlice'
import { getOneFromDb, writeQuizData } from '../../service/firebase'
import { ArrowRight } from '../../svg/SvgApi'
import GiveLink from '../GiveLink/GiveLink'
import Navbar from '../Navbar/Navbar'
import NewQuiz from './NewQuiz'

const CreateQuiz = () => {
	const [isQuizName, setIsQuizName] = useState('')
	const [isGiveLink, setIsGiveLink] = useState({
		isOpen: false,
		data: null,
	})
	const [questions, setQuestions] = useState([
		{
			id: 0,
			questionText: '',
			answersOptions: [
				{ answerText: '', isCorrect: false },
				{ answerText: '', isCorrect: false },
				{ answerText: '', isCorrect: false },
				{ answerText: '', isCorrect: false },
			],
		},
	])

	const { user } = useSelector(selectUser)

	const changeQuestion = (id, type, value) => {
		const index = questions.findIndex(obj => obj.id === id)

		const _questions = [...questions]

		switch (type) {
			case 'title':
				_questions[index].questionText = value
				break
			case 'a':
				_questions[index].answersOptions[0].answerText = value
				break
			case 'b':
				_questions[index].answersOptions[1].answerText = value
				break
			case 'c':
				_questions[index].answersOptions[2].answerText = value
				break
			case 'd':
				_questions[index].answersOptions[3].answerText = value
				break

			case 'correct':
				if (value === 'a') {
					_questions[index].answersOptions[0].isCorrect = true
					_questions[index].answersOptions[1].isCorrect = false
					_questions[index].answersOptions[2].isCorrect = false
					_questions[index].answersOptions[3].isCorrect = false
				}
				if (value === 'b') {
					_questions[index].answersOptions[0].isCorrect = false
					_questions[index].answersOptions[1].isCorrect = true
					_questions[index].answersOptions[2].isCorrect = false
					_questions[index].answersOptions[3].isCorrect = false
				}
				if (value === 'c') {
					_questions[index].answersOptions[0].isCorrect = false
					_questions[index].answersOptions[1].isCorrect = false
					_questions[index].answersOptions[2].isCorrect = true
					_questions[index].answersOptions[3].isCorrect = false
				}
				if (value === 'd') {
					_questions[index].answersOptions[0].isCorrect = false
					_questions[index].answersOptions[1].isCorrect = false
					_questions[index].answersOptions[2].isCorrect = false
					_questions[index].answersOptions[3].isCorrect = true
				}
				break
			default:
				break
		}

		setQuestions(_questions)
	}

	const addQuestion = () => {
		const id = questions.length

		const newQuestion = {
			id: id + 1,
			questionText: '',
			answersOptions: [
				{ answerText: '', isCorrect: false },
				{ answerText: '', isCorrect: false },
				{ answerText: '', isCorrect: false },
				{ answerText: '', isCorrect: false },
			],
		}

		setQuestions(prev => [...prev, newQuestion])
	}

	const uploadQuiz = () => {
		if (isQuizName.trim() === '') {
			return alert('  ')
		}

		for (let i = 0; i < questions.length; i++) {
			if (questions[i].questionText.trim() === '') {
				return alert('  ')
			}

			for (let j = 0; j < questions[i].answersOptions.length; j++) {
				if (questions[i].answersOptions[j].answerText.trim() === '') {
					return alert('  ')
				}
			}
		}

		const quizId = new Date().getTime()
		moment.locale('ru')
		const createdAt = moment().format('LL')

		writeQuizData(user.uid, quizId, {
			name: isQuizName,
			quiz: questions,
			quizId: quizId,
			createdAt: createdAt,
		})
		setIsGiveLink({
			isOpen: true,
			data: {
				id: user.uid,
				quizId: quizId,
			},
		})
	}

	return (
		<Wrapper>
			<Navbar />
			{!isGiveLink.isOpen ? (
				<Container className='container'>
					<Title className='mt-5'> </Title>
					<SubTitle className='text-secondary border-bottom border-2 pb-3'>
						Lorem ipsum, dolor sit amet consectetur adipisicing elit. Sit
						accusantium eius rerum saepe aperiam qui voluptas aliquid quia sequi
						cupiditate.
					</SubTitle>

					<input
						value={isQuizName}
						onChange={e => setIsQuizName(e.target.value)}
						className='form-control text-center fs-2'
						type='text'
						placeholder='  '
					/>

					{questions.map((item, index) => {
						return (
							<NewQuiz
								index={index}
								id={item.id}
								changeQuestion={changeQuestion}
								key={index}
							/>
						)
					})}

					<Button
						onClick={addQuestion}
						className='btn w-100 my-4 p-3 fs-5 text-center text-primary fw-bold'
					>
						  +
					</Button>

					<Container className='d-flex justify-content-center align-items-center'>
						<Button
							onClick={uploadQuiz}
							className='d-inline-flex justify-content-center align-items-center btn relative bg-primary mb-5 p-3 fs-5 text-start text-light w-50'
						>
							<p className='p-0 m-0'> </p>
							<ArrowRight className='ms-auto' />
						</Button>
					</Container>
				</Container>
			) : (
				<GiveLink id={isGiveLink.data.id} quizId={isGiveLink.data.quizId} />
			)}
		</Wrapper>
	)
}

const Wrapper = styled.div`
	background-color: #fff;
`

const Container = styled.div``

const Title = styled.h1``

const SubTitle = styled.p``

const Button = styled.button`
	background-color: #e9f0fe;
	border: none;
	outline: none;
`

export default CreateQuiz
