import React from 'react'
import styled from 'styled-components'

const QuizBottom = ({ questions, selectAnswer }) => {
	return (
		<Wrapper>
			<Row className='row m-4'>
				<Col
					onClick={() =>
						selectAnswer(questions[0].answerText, questions[0].isCorrect)
					}
					className='col p-3 border border-2 border-primary text-center border shadow mx-2'
				>
					{questions[0].answerText}
				</Col>
				<Col
					onClick={() =>
						selectAnswer(questions[1].answerText, questions[1].isCorrect)
					}
					className='col p-3 border border-2 border-primary text-center border shadow mx-2'
				>
					{questions[1].answerText}
				</Col>
			</Row>
			<Row className='row m-4'>
				<Col
					onClick={() =>
						selectAnswer(questions[2].answerText, questions[2].isCorrect)
					}
					className='col p-3 border border-2 border-primary text-center border shadow mx-2'
				>
					{questions[2].answerText}
				</Col>
				<Col
					onClick={() =>
						selectAnswer(questions[3].answerText, questions[3].isCorrect)
					}
					className='col p-3 border border-2 border-primary text-center border shadow mx-2'
				>
					{questions[3].answerText}
				</Col>
			</Row>

			<LastTitle className='text-center mt-5 pt-5 text-primary'>
				 1
			</LastTitle>
		</Wrapper>
	)
}

const Row = styled.div``

const Col = styled.div`
	border-radius: 30px;
	cursor: pointer;

	transition: all ease 0.2s;
	&:hover {
		background-color: #0275d8;
		color: #fff;
	}
`

const Wrapper = styled.div`
	margin-top: 50px;
`

const LastTitle = styled.p``

export default QuizBottom
