import { createSlice } from '@reduxjs/toolkit'

const initialState = {
	profilePopup: false,
}

const profilePopupSlice = createSlice({
	name: 'profilePopup',
	initialState,
	reducers: {
		setProfilePopup: (state, action) => {
			state.profilePopup = action.payload
		},
	},
})

export const { setProfilePopup } = profilePopupSlice.actions

export const selectProfilePopup = state => state.profilePopup

export default profilePopupSlice.reducer
