import React from 'react'
import Navbar from '../Navbar/Navbar'
import styled from 'styled-components'
import boss from '../../assets/boss.jpeg'
import { Facebook, Instagram, Tiktok, Twitter } from './icon'
import firstImg from '../../assets/1.jpg'
import secondImg from '../../assets/2.jpg'
import thirdImg from '../../assets/3.jpg'
import Slider from './Slider'

const About = () => {
	return (
		<>
			<Navbar />

			<Wrapper className='bg-light'>
				<AboutWrapper className='container'>
					<img className='border shadow' src={boss} alt='Boss' />
					<AboutMe>
						<h1 className='text-danger mt-3'>  </h1>
						<h5 className='mt-4'>Web-design/Photoshop i.</h5>
						<p className='w-75 text-secondary mt-3 fs-5'>
							    , 
							-    .
						</p>

						<SocialLinks className='mt-3'>
							<Instagram className='mx-1' />
							<Facebook className='mx-1' />
							<Tiktok className='mx-1' />
							<Twitter className='mx-1' />
						</SocialLinks>
					</AboutMe>
				</AboutWrapper>
			</Wrapper>
		</>
	)
}

const Wrapper = styled.div`
	width: 100%;
	min-height: 100vh;
`

const AboutWrapper = styled.div`
	display: flex;

	padding-top: 100px;

	img {
		width: 370px;
		height: 370px;
		object-fit: cover;
	}

	@media (max-width: 958px) {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
`

const AboutMe = styled.div`
	padding-left: 50px;

	@media (max-width: 958px) {
		margin-top: 50px;

		display: flex;
		align-items: center;
		flex-direction: column;
		text-align: center;
	}
`

const SocialLinks = styled.div``

const ShowCase = styled.div``

const Card = styled.div`
	width: 300px;
	height: 300px;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
`

export default About
