import React from 'react'
import styled from 'styled-components'
import { NineDots, SixDots, IsTrueIcon, Xicon } from '../../svg/SvgApi'

const NewQuiz = ({ id, changeQuestion, isTrue, index }) => {
	return (
		<Wrapper>
			<Header>
				<NineDots />
				<h3 className='ps-3 pt-1'> {index + 1}</h3>
				<Button className='ms-auto fs-3'>
					<Xicon />
				</Button>
			</Header>
			<Input
				onChange={e => changeQuestion(id, 'title', e.target.value)}
				type='text'
				placeholder=' '
			/>
			<Answer>
				<SixDots />
				<h5 className='text-secondary ps-2 pe-3 pt-2'>A</h5>
				<AnswerInput
					onChange={e => changeQuestion(id, 'a', e.target.value)}
					type='text'
					placeholder=' '
				/>
				<CorrectIcon isTrue={isTrue} className='border border-1'>
					<IsTrueIcon />
				</CorrectIcon>
			</Answer>
			<Answer>
				<SixDots />
				<h5 className='text-secondary ps-2 pe-3 pt-2'>B</h5>
				<AnswerInput
					onChange={e => changeQuestion(id, 'b', e.target.value)}
					type='text'
					placeholder=' '
				/>
				<CorrectIcon isTrue={isTrue} className='border border-1'>
					<IsTrueIcon />
				</CorrectIcon>
			</Answer>
			<Answer>
				<SixDots />
				<h5 className='text-secondary ps-2 pe-3 pt-2'>C</h5>
				<AnswerInput
					onChange={e => changeQuestion(id, 'c', e.target.value)}
					type='text'
					placeholder=' '
				/>
				<CorrectIcon isTrue={isTrue} className='border border-1'>
					<IsTrueIcon />
				</CorrectIcon>
			</Answer>
			<Answer>
				<SixDots />
				<h5 className='text-secondary ps-2 pe-3 pt-2'>D</h5>
				<AnswerInput
					onChange={e => changeQuestion(id, 'd', e.target.value)}
					type='text'
					placeholder=' '
				/>
				<CorrectIcon isTrue={isTrue} className='border border-1'>
					<IsTrueIcon />
				</CorrectIcon>
			</Answer>

			<h5 className='my-3'>  </h5>
			<Select
				className='form-control w-50'
				onChange={e => changeQuestion(id, 'correct', e.target.value)}
				defaultValue={'choose'}
			>
				<option value='choose'>  </option>
				<option value='a'>A</option>
				<option value='b'>B</option>
				<option value='c'>C</option>
				<option value='d'>D</option>
			</Select>
		</Wrapper>
	)
}

const Wrapper = styled.div`
	background-color: #f1f3f6;
	padding: 50px 200px;
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	margin: 30px 0px;
`

const Header = styled.div`
	width: 100%;
	display: inline-flex;
	align-items: center;
`

const Input = styled.input`
	width: 100%;
	outline: none;
	padding: 15px;
	margin: 15px 0px;
	font-size: 18px;
`

const Answer = styled.div`
	width: 100%;
	display: inline-flex;
	align-items: center;
	position: relative;
	padding: 8px 0px;
`

const AnswerInput = styled.input`
	width: 100%;
	outline: none;
	padding: 10px;
`

const CorrectIcon = styled.div`
	position: absolute;
	padding: 4px;
	background-color: ${({ isTrue }) => (isTrue ? 'red' : '#daf0ea')};
	right: 0;
	margin-right: 6px;
	cursor: pointer;
`

const Select = styled.select`
	padding: 10px;
	outline: none;
`

const Button = styled.button`
	background-color: #e9f0fe;
	border: none;
	outline: none;
`

export default NewQuiz
