import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { selectUser } from '../../features/userSlice'
import { getAllQuizFromDb } from '../../service/firebase'

import Navbar from '../Navbar/Navbar'
import { EnterIcon } from './Icon'

import backgroundImg from '../../assets/5586938.jpg'
import { useNavigate } from 'react-router-dom'

const Profile = () => {
	const { user } = useSelector(selectUser)
	const [quiz, setQuiz] = useState([])

	let navigate = useNavigate()

	useEffect(() => {
		getAllQuizFromDb(user.uid).then(res => {
			if (!res) {
				setQuiz(null)
			} else {
				const arrayOfObj = Object.entries(res).map(e => ({ ...e[1] }))
				setQuiz(arrayOfObj)
			}
		})
	}, [])

	return (
		<>
			<Navbar />

			<Wrapper className='bg-white'>
				<ProfileWrapper>
					<Container className='container'>
						<ProfileInfo>
							<img
								className='border border-2 rounded-circle'
								src={user.photoURL}
								alt={user.displayName}
							/>

							<h2 className='mt-2'>{user.displayName}</h2>
						</ProfileInfo>
					</Container>
				</ProfileWrapper>

				<Main className='container'>
					<h3> </h3>

					{quiz ? (
						<ul className='my-5 mx-auto list-group w-100'>
							{quiz.map((item, index) => {
								return (
									<li
										key={index}
										className='list-group-item shadow fs-5 py-4 px-5 my-2 d-inline-flex align-items-center'
									>
										<EnterIcon className='me-4' />
										<QuizTitle>
											<h3 className='my-3'>{item.name}</h3>
											<button
												onClick={() =>
													navigate(`/profile/quiz/${user.uid}/${item.quizId}`)
												}
												className='btn btn-primary'
											>
												 
											</button>
										</QuizTitle>
										<CirclesWrapper>
											<Circles>
												<img
													className='rounded-circle border border-2'
													src='https://cheaterboss.com/wp-content/uploads/2022/02/1644520379611.png'
													alt='user'
												/>
												<img
													className='rounded-circle border border-2'
													src='https://1tb.favim.com/preview/8/804/8040/80400/8040066.jpg'
													alt='user'
												/>
												<img
													className='rounded-circle border border-2'
													src='https://expertphotography.b-cdn.net/wp-content/uploads/2018/10/cool-profile-pictures-retouching-1.jpg'
													alt='user'
												/>
											</Circles>
											<small className='text-secondary'>32 </small>
										</CirclesWrapper>
										<span className='ms-2'>{item.createdAt}</span>
									</li>
								)
							})}
						</ul>
					) : (
						<p className='text-center fs-3 mt-5 pt-5'>    </p>
					)}
				</Main>
			</Wrapper>
		</>
	)
}

const Wrapper = styled.div`
	width: 100%;
	min-height: 100vh;
`

const ProfileWrapper = styled.div`
	height: 300px;
	background-image: url(${backgroundImg});
	background-size: cover;
`

const Container = styled.div`
	position: relative;
	width: 100%;
	height: 100%;
`

const ProfileInfo = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	bottom: -110px;
	flex-direction: column;

	img {
		width: 140px;
		height: 140px;
		object-fit: cover;
	}
`

const Main = styled.div`
	margin-top: 150px;
`

const QuizTitle = styled.div`
	width: 300px;
	text-overflow: clip;
`

const CirclesWrapper = styled.div`
	margin-left: 170px;

	@media (max-width: 988px) {
		small {
			font-size: 18px;
			margin-right: 40px;
		}
	}
`

const Circles = styled.div`
	img {
		width: 50px;
		height: 50px;
		object-fit: cover;

		z-index: 10;

		&:nth-child(2) {
			z-index: 20;
			transform: translateX(-20px);
		}

		&:nth-child(3) {
			z-index: 30;
			transform: translateX(-37px);
		}
	}

	@media (max-width: 988px) {
		display: none;
	}
`

export default Profile
